body {
  font-family: 100% Arial, sans-serif;
  margin: 0;
  color: black; }

main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: 100vh; }

div {
  border: 5px solid black;
  border-radius: 5px;
  width: 100px;
  height: 100px;
  padding: 20px;
  margin: 40px;
  font-size: 35px;
  font-family: 'Patrick Hand', cursive;
  transition: 0.4s transform;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0 2px dodgerblue, 0 0 14px dodgerblue; }
  div:hover {
    transform: scale(1.2) rotate(90deg); }

div:nth-of-type(1) {
  background: rgba(30, 144, 255, 0.93); }

div:nth-of-type(2) {
  background: rgba(30, 144, 255, 0.86); }

div:nth-of-type(3) {
  background: rgba(30, 144, 255, 0.79); }

div:nth-of-type(4) {
  background: rgba(30, 144, 255, 0.72); }

div:nth-of-type(5) {
  background: rgba(30, 144, 255, 0.65); }

div:nth-of-type(6) {
  background: rgba(30, 144, 255, 0.58); }

div:nth-of-type(7) {
  background: rgba(30, 144, 255, 0.51); }

div:nth-of-type(8) {
  background: rgba(30, 144, 255, 0.44); }

div:nth-of-type(9) {
  background: rgba(30, 144, 255, 0.37); }

div:nth-of-type(10) {
  background: rgba(30, 144, 255, 0.3); }
